/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap');

.HomePage_1stSection__container {
  display: flex;
  min-height: 100vh; /* Adjusted for better responsiveness */
  background-image: url("https://ik.imagekit.io/emtbd/emt_images/images/DALL_E%202024-05-05%2015.56.56%20-%20Create%20a%20professional%20and%20abstract%20animated%20background%20suitable%20for%20a%20medical%20company's%20website.%20The%20animation%20should%20feature%20soft%20light%20colors,%20conve.webp?updatedAt=1714895911063");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.HomePage_1stSection__writings {
  min-height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  padding: 0.5em;
  margin: 5em;
}

.HomePage_1stSection__writings p {
  margin-top: 0.7em;
}

/* Style for the first section */
.HomePage_1stSection__writings-1st_section {
  background-color: #d8ecf5; /* Light background for readability */
  width:55%;
  z-index:1 ;
  padding: 1em;
  margin:1em;
  box-shadow: 10px 10px 5px 5px rgba(0, 0, 0, 0.3);
}

/* Style for the second section */
.HomePage_1stSection__writings-2nd_section {
  background-color: #f1f1e5; /* Same as first for consistency */
  width: 45%;
  padding: 1em;
  margin:8em 10em 0em 0em;
  z-index: 2;
  box-shadow: 10px 10px 5px 5px rgba(0, 0, 0, 0.3);
}

/* Common styles for text wrappers */
/* Moderate Version */
.HomePage_1stSection__writings-1st_section_textwrapper,
.HomePage_1stSection__writings-2nd_section_textwrapper {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.2rem; /* Slightly larger for better readability */
  color: rgb(1, 10, 20);
  margin-top: 1.5em;
  line-height: 2em; /* Increased line spacing */
}

/* Title styling */
.HomePage_1stSection__title {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.6rem; /* Larger for emphasis */
  font-weight: bold;
  color: #005792; /* Dark blue for professional tone */
}

/* Subtitle styling */
.HomePage_1stSection__subtitle {
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  font-weight: 500;
  color: #045a12; /* Subdued blue for subtlety */
  margin-bottom: 10px;
}

.HomePage_1stSection__description {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-size: 1.2rem; /* Maintaining base font size */
  color: #000000; /* Standard dark grey */
  text-align: justify;
  line-height: 1.8; /* Increased from 1.6 for better readability */
}






@media only screen and (max-width: 1500px) and (min-width: 1380px) {
  .HomePage_1stSection__writings{
    margin: 3em 0em 0em 5em;
    padding: 0.2em;
  }

  .HomePage_1stSection__title {
    font-size: 1.5rem; /* Ensure title is still prominent */
  }

  .HomePage_1stSection__subtitle {
    font-size: 1.2rem; /* Adjust subtitle size for readability */
  }

  .HomePage_1stSection__description {
    font-size: 1.1rem; /* Adjust description text size for smaller screens */
    line-height: 1.6; /* Adjust line height to maintain readability */
  }
}



@media only screen and (max-width: 1379px) {
  
  .HomePage_1stSection__writings{
    margin: 1em 0em 0em 5em;
    padding: 0.2em;

  }

  .HomePage_1stSection__title {
    font-size: 1.4rem; /* Ensure title is still prominent */
  }

  .HomePage_1stSection__subtitle {
    font-size: 1.1rem; /* Adjust subtitle size for readability */
  }

  .HomePage_1stSection__description {
    font-size: 1rem; /* Adjust description text size for smaller screens */
    line-height: 1.6; /* Adjust line height to maintain readability */
  }
}

@media only screen and (max-width: 1050px) {
  .HomePage_1stSection__container {
      min-height: auto;
      flex-direction: column; /* Stack sections vertically for better readability */
      align-items: center; /* Center-align content for a visually appealing layout */
  }

  .HomePage_1stSection__writings {
      flex-direction: column; /* Stack the writing sections vertically */
      margin: 0.5em 0em 1em 0em; /* Provide ample space around the content for a clean look */
      padding: 1em; /* Ensure sufficient padding for touch interactions */
  }

  .HomePage_1stSection__writings-1st_section,
  .HomePage_1stSection__writings-2nd_section {
      width:80%; /* Adjust width to take up most of the screen but leave some margin */
      margin: 1em auto; /* Center and add margin for separation */
      box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.2); /* Slightly lighter shadow for a softer effect */
  }

  .HomePage_1stSection__title {
      font-size: 1.3rem; /* Slightly smaller font size for titles */
  }

  .HomePage_1stSection__subtitle {
      font-size: 1.0rem; /* Reduce subtitle font size for proportionality */
  }

  .HomePage_1stSection__description {
      font-size: 1rem; /* Reduce text size for better readability */
      line-height: 1.5; /* Adjust line height for better readability */
      text-align: left; /* Align text to the left for a more natural reading flow */
   
  }
}


























