/* Services.css */
.services__container {
    background-color: #ffffff;  /* Ensure a clean, white background */
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 70vh;
    padding: 2em;
    font-family: 'Roboto', sans-serif;  /* Unified font for clean design */
}

.services__heading-container {
    margin-left: 2em;
    flex-basis: 30%;  /* Adjust this to control the spacing */
    display: flex;
    justify-content: flex-start;
    align-items: center;
    min-height:inherit;
}

.services__heading {
    font-size: 2.5rem;
    color: #10375c;
    margin: 0 20px;
}

.services__features-container {
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    /* Adjust padding to visually separate from the border */
}

.services__feature {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    line-height: 3rem;
    max-width: 310px;
    margin: 0.5em;
    background: #f9f9f9;  /* Light background for each card */
    padding: 1em;
    border-radius: 10px;  /* Rounded corners for a modern look */
    box-shadow: 5px 10px 10px rgba(0,0,0,0.1);  /* Subtle shadow for depth */
    transition: transform 0.3s ease, box-shadow 0.3s ease;  /* Smooth transitions for hover effects */
}

.services__feature:hover {
    transform: translateY(-5px);  /* Slight lift effect */
    box-shadow: 0 8px 12px rgba(0,0,0,0.2);  /* Increased shadow for lifted effect */
}

.services__icon {
    margin-bottom: 15px;
    font-size: 4rem;  /* Ensuring uniform icon size */
    color: #4a90e2;  /* Unified color for icons for a sleek look */
}

.services__content h2 {
    margin: 5px 0;
    color: #10375c;
    font-size: 1.5rem;
}

.services__content p {
    color: #666;
    font-size: 1.1rem;
    line-height: 1.6rem;
}


@media only screen and (max-width: 1500px) and (min-width: 1380px) {
    .services__container {
        padding: 1em; /* Reduce padding */
    }

    .services__heading-container {
        flex-basis: 35%; 
        margin-left: 1em; /* Reduce margin */
       
    }

    .services__heading {
        font-size: 2rem; /* Adjust font size to fit smaller screens */
        text-align: center; /* Center the heading text */
        margin: 10px 0; /* Adjust margin */
    }


    .services__feature {
        max-width: 250px;
    }

    .services__icon {
        font-size: 2.5rem; /* Slightly reduce icon size for space efficiency */
    }

    .services__content h2 {
        font-size: 1.2rem; /* Adjust heading size in content */
    }

    .services__content p {
        font-size: 1rem; /* Adjust paragraph size for readability */
    }
}


@media only screen and (max-width: 1379px) {
    .services__container {
        padding: 1em; /* Reduce padding */
    }

    .services__heading-container {
        flex-basis: 35%; 
        margin-left: 1em; /* Reduce margin */
       
    }

    .services__heading {
        font-size: 2rem; /* Adjust font size to fit smaller screens */
        text-align: center; /* Center the heading text */
        margin: 10px 0; /* Adjust margin */
    }


    .services__feature {
        max-width: 250px;
    }

    .services__icon {
        font-size: 2.5rem; /* Slightly reduce icon size for space efficiency */
    }

    .services__content h2 {
        font-size: 1.2rem; /* Adjust heading size in content */
    }

    .services__content p {
        font-size: 1rem; /* Adjust paragraph size for readability */
    }
}

@media (max-width: 1050px){

    .services__container {
        flex-direction: column;
        min-height: auto;
        padding: 1em; /* Reduce padding */
    }

    .services__heading-container {
        flex-basis: 45%; 
        margin: 0em 0em 3em 1em;
    }

    .services__heading {
        font-size: 2rem; /* Adjust font size to fit smaller screens */
        text-align: center; /* Center the heading text */
        margin: 10px 0; /* Adjust margin */
    }
    .services__feature {
        max-width: 250px;
    }

    .services__icon {
        font-size: 2.0rem; /* Slightly reduce icon size for space efficiency */
    }

    .services__content h2 {
        font-size: 1.1rem; /* Adjust heading size in content */
    }

    .services__content p {
        font-size: 1rem; /* Adjust paragraph size for readability */
    }

}

