/* Import Normalize.css and Google Fonts */
@import url('https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');

/* Specific styles for Contact Us page */
.Contact_Us {
    font-family: 'Lato', sans-serif;
    color: #333;
    background: #f4f4f9; /* Subtle background color */
}

/* Adjusting the entire container for symmetry and padding */
.Contact_Us-container {
    display: flex;
    justify-content: space-evenly;
    padding: 2em;
    gap: 20px;
    flex-wrap: wrap;
    align-items: center; /* Align items vertically for better visual alignment */
}

.contact-us__form-container {
    width: 35%;
    background: #ffffff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12);
    margin: 1em;
}

.contact-us__form__succeeded {
    max-width:100%;
    padding: 1.5rem;
    margin-top: 1rem;
    background-color: #e8f0fe; /* Subtle blue background */
    color: #044997; /* Darker blue text color for contrast */
    font-size: 1.2rem; /* Larger font size for better readability */
    text-align: center; /* Center the text */
    border-radius: 10px; /* Rounded corners */
    box-shadow: none; /* Remove any box shadow */
    border: 1px solid #b6d4fe; /* Soft blue border */
}

.contact-us__reach-out {
    width: 35%;
    min-width: 30%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    background: #ffffff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12);
    margin: 1rem;
}

/* Form styling */
.contact-us__form-group {
    margin-bottom: 1.5rem;
}

.contact-us__label {
    font-weight: 700; /* Bold labels */
    margin-bottom: 0.5rem;
    color: #000000; /* Accent color for labels */
}

.contact-us__input, .contact-us__select, .contact-us__textarea {
    margin-top: 0.5em;
    width: 100%;
    padding: 0.6rem;
    border-radius: 5px;
    border: 1px solid #007BFF; /* Accent border */
    background: #eef1f7; /* Subtle background */
}

.contact-us__textarea {
    height: 150px; /* Fixed height for textarea */
}

.contact-us__submit-button {
    width: 100%;
    padding: 1em;
    background-color: #0056b8; /* Button color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.contact-us__submit-button:hover {
    background-color: #003a70; /* Darker blue on hover */
}



/* Icons and reach out section adjustments */
.ContactUs__icon {
    margin-bottom:0.5em; /* Space between icon and text */
    color: #007BFF; /* Icon color */
    font-size: 2.2em;
}

.office_elements h4 {
    font-family: 'Guardian Sans Bold', serif;
    font-size: 1.2rem; /* Slightly larger font size for title */
    color: #10375c;
    margin-bottom: 1rem; /* Space between sections */
}

.office_elements p {
    font-family: 'Guardian Sans Light', serif;
    margin-bottom: 1em;
    font-size: 1.2rem;
    color: #080808; /* Lighter text color for descriptions */
}



.contact-us__google-map {
    width: 100%;
    min-height: 70vh;
    border-radius: 8px;
    margin-top: 1rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .Contact_Us-container {
        flex-direction: column;
    }
    .contact-us__form-container, .contact-us__reach-out {
        width: 90%;
    }
}



@media only screen and (max-width: 1500px) and (min-width: 1380px) {
    .office_elements h4, .office_elements p, .contact-us__form-group label {
        font-size: 1.1rem; /* Slightly reduce font size for better readability */
    }

    .ContactUs__icon {
        margin-bottom: 0.3em; /* Adjust icon spacing */
        font-size: 1.7em;
    }
   
}

@media only screen and (max-width: 1379px) {

.contact-us__form-container{
    min-width: 45%;
}
.contact-us__reach-out{
    min-width: 40%;

}

.office_elements h4, .office_elements p, .contact-us__form-group label {
    font-size: 1.0rem; /* Slightly reduce font size for better readability */
}

.ContactUs__icon {
    margin-bottom: 0.3em; /* Adjust icon spacing */
    font-size: 1.5em;
}

.contact-us__google-map {
    min-height: 50vh; /* Adjust map height to be less dominating on the layout */
}
.contact-us__form__succeeded{
    font-size: 1.0rem;
}
}

@media only screen and (max-width: 1050px)  {
    .Contact_Us-container {
        flex-direction: column; /* Switch to column layout */
        align-items: center; /* Center align items for consistency */
        padding: 0.7em; /* Adjust padding for a tighter layout */
    }

    .contact-us__form-container, .contact-us__reach-out {
        width: 100%; /* Full width to utilize the available space */
        min-width: auto; /* Override any previous min-width settings */
        max-width: 600px; /* Limit maximum width for better readability */
        margin: 0.5em 0; /* Adjust margin for vertical stacking */
        padding: 1.5rem; /* Provide ample padding for touch interactions */
    }

    .ContactUs__icon {
        font-size: 1.5em; /* Adjust icon size for better visibility */
        margin-bottom: 10px; /* Provide space between icon and text */
    }

    .office_elements h4, .office_elements p, .contact-us__form-group label {
        font-size: 1rem; /* Slightly reduce font size for space efficiency */
    }

    .contact-us__google-map {
        width: 100%; /* Full width map */
        min-height: 500px; /* Adjust height for compact display */
    }

    .contact-us__form__succeeded {
        font-size: 1rem; /* Adjust font size for success message */
        max-width: 90%; /* Adjust width for better fit within the container */
        padding: 1em; /* Adjust padding for a balanced appearance */
    }

    .contact-us__submit-button, .contact-us__input, .contact-us__select, .contact-us__textarea {
        padding: 0.8em; /* Adjust padding for easier interaction */
    }

    .contact-us__input, .contact-us__select, .contact-us__textarea {
        margin-top: 0.5em;
        width: 100%;
        padding: 0.5rem;
        border-radius: 5px;
        border: 1px solid #007BFF; /* Accent border */
        background: #eef1f7; /* Subtle background */
    }
    
    .contact-us__textarea {
        height: 100px; /* Fixed height for textarea */
    }
}
